<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="row mt-4">
      <div class="col-md-4 mb-4" v-for="item in products" :key="item.id">
        <div class="card border-0 shadow-sm">
          <div style="height: 150px; background-size: cover; background-position: center"
            :style="{backgroundImage: `url(${item.imageUrl})`}">
          </div>
          <div class="card-body">
            <span class="badge badge-secondary float-right ml-2">{{ item.category }}</span>
            <h5 class="card-title">
              <a href="#" class="text-dark">{{ item.title }}</a>
            </h5>
            <p class="card-text">{{ item.content }}</p>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h5" v-if="!item.price">{{ item.origin_price }} 元</div>
              <del class="h6" v-if="item.price">原價 {{ item.origin_price }} 元</del>
              <div class="h5" v-if="item.price">現在只要 {{ item.price }} 元</div>
            </div>
          </div>
          <div class="card-footer d-flex">
            <button type="button" class="btn btn-outline-secondary btn-sm"
              @click="getProduct(item.id)">
              <i class="fas fa-spinner fa-spin" v-if="status.loadingItem == item.id"></i>
              查看更多
            </button>
            <button type="button" class="btn btn-outline-danger btn-sm ml-auto"
              @click="addToCart(item.id)">
              <i class="fas fa-spinner fa-spin" v-if="status.loadingToCart == item.id"></i>
              加到購物車
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- 商品詳細資訊 -->
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">{{ product.title }}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <img :src="product.imageUrl" class="img-fluid" alt="">
            <blockquote class="blockquote mt-3">
              <p class="mb-0">{{ product.content }}</p>
              <footer class="blockquote-footer text-right">{{ product.description }}</footer>
            </blockquote>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h4" v-if="!product.price">{{ product.origin_price }} 元</div>
              <del class="h6" v-if="product.price">原價 {{ product.origin_price }} 元</del>
              <div class="h4" v-if="product.price">現在只要 {{ product.price }} 元</div>
            </div>
            <select name="" class="form-control mt-3" v-model="product.num">
              <option :value="num" v-for="num in 10" :key="num">
                  選購 {{num}} {{product.unit}}
              </option>
            </select>
          </div>
          <div class="modal-footer">
            <div class="text-muted text-nowrap mr-3">
            小計 <strong>{{ product.num * product.price }}</strong> 元
            </div>
            <button type="button" class="btn btn-primary"
              @click="addToCart(product.id, product.num)">
            <i class="fas fa-spinner fa-spin" v-if="product.id === status.loadingItem"></i>
            加到購物車
            </button>
          </div>
        </div>
      </div>
    </div>

    <pagination emitMethod='emitGetCustomerOrders' :page="pagination" v-on:emitGetCustomerOrders="getProducts"></pagination>

    <!-- 購物車 -->
    <div v-if="cart.hasOwnProperty('carts') && cart.carts.length > 0">
      <div class="my-5 row-cols-2 row justify-content-center">
        <table class="table">
          <thead>
            <th></th>
            <th>品名</th>
            <th>數量</th>
            <th>單價</th>
          </thead>
          <tbody>
            <tr v-for="item in cart.carts" :key="item.id">
              <td class="align-middle">
                <button type="button" class="btn btn-outline-danger btn-sm"
                  @click="removeCartItem(item.id)">
                  <i class="far fa-trash-alt"></i>
                </button>
              </td>
              <td class="align-middle">
                {{ item.product.title }}
                <div class="text-success" v-if="item.coupon">
                  已套用優惠券
                </div>
              </td>
              <td class="align-middle">{{ item.qty }} / {{ item.product.unit }}</td>
              <td class="align-middle text-right">{{ item.final_total }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="3" class="text-right">總計</td>
              <td class="text-right">{{ cart.total }}</td>
            </tr>
            <tr v-if="cart.total != cart.final_total">
              <td colspan="3" class="text-right text-success">折扣價</td>
              <td class="text-right text-success">{{ cart.final_total }}</td>
            </tr>
          </tfoot>
        </table>
      </div>
      <div class="my-5 row-cols-2 row justify-content-center">
        <div class="input-group mb-3 input-group-sm">
          <input type="text" class="form-control" placeholder="請輸入優惠碼" v-model="couponCode">
          <div class="input-group-append">
            <button class="btn btn-primary" type="button" :disabled="couponCode==''" @click="useCoupon">
              套用優惠碼
            </button>
          </div>
        </div>
      </div>
      <div class="my-5 row justify-content-center">
        <validation-observer class="col-md-6" v-slot="{ invalid }">
          <form @submit.prevent="createOrder">
            <validation-provider rules="required|email" v-slot="{ errors, classes }">
              <div class="form-group">
                <label for="email">Email</label>
                <input id="email" type="email" name="email" v-model="form.user.email"
                  placeholder="請輸入 Email"
                  class="form-control" :class="classes">
                <span class="invalid-feedback">{{ errors[0] }}</span>
              </div>
            </validation-provider>

            <validation-provider rules="required|name" v-slot="{ errors, classes }">
              <div class="form-group">
                <label for="username">收件人姓名</label>
                <input id="username" type="text" name="姓名" v-model="form.user.name"
                  placeholder="請輸入收件人姓名"
                  class="form-control" :class="classes">
                <span class="invalid-feedback">{{ errors[0] }}</span>
              </div>
            </validation-provider>

            <validation-provider rules="required|digits:10" v-slot="{ errors, classes }">
              <div class="form-group">
                <label for="usertel">收件人電話</label>
                <input id="usertel" type="text" name="電話" v-model="form.user.tel"
                  placeholder="請輸入電話"
                  class="form-control" :class="classes">
                <span class="invalid-feedback">{{ errors[0] }}</span>
              </div>
            </validation-provider>

            <validation-provider rules="required|address" v-slot="{ errors, classes }">
              <div class="form-group">
                <label for="useraddress">收件人地址</label>
                <input id="useraddress" type="text" name="地址" v-model="form.user.address"
                  placeholder="請輸入地址"
                  class="form-control" :class="classes">
                <span class="invalid-feedback">{{ errors[0] }}</span>
              </div>
            </validation-provider>

            <div class="form-group">
              <label for="message">留言</label>
              <textarea id="message" class="form-control" cols="30" rows="10"
              v-model="form.message"></textarea>
            </div>

            <div class="text-right">
              <button class="btn btn-danger" :disabled="invalid">送出訂單</button>
            </div>
          </form>
        </validation-observer>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import pagination from './Pagination'

export default {
  data () {
    return {
      products: [],
      pagination: {},
      product: {},
      cart: {},
      couponCode: '',
      isLoading: false,
      status: {
        loadingItem: '',
        loadingToCart: ''
      },
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: ''
        },
        message: ''
      }
    }
  },
  components: {
    pagination
  },
  methods: {
    getProducts (page = 1) {
      const vm = this
      const api = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/products?page=${page}`
      vm.isLoading = true
      this.$http.get(api).then(response => {
        vm.isLoading = false
        if (response.data.success) {
          vm.products = response.data.products
          vm.pagination = response.data.pagination
        } else {
          this.$bus.$emit('message:push', `取得商品列表失敗: ${response.data.message}`, 'danger')
        }
      })
    },
    getProduct (id) {
      const vm = this
      const api = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/product/${id}`
      vm.status.loadingItem = id
      this.$http.get(api).then(response => {
        vm.status.loadingItem = ''
        if (response.data.success) {
          response.data.product.num = 1
          vm.product = response.data.product
          $('#productModal').modal('show')
        } else {
          this.$bus.$emit('message:push', `取得商品資訊失敗: ${response.data.message}`, 'danger')
        }
      })
    },
    getCart () {
      const vm = this
      const api = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/cart`
      vm.isLoading = true
      this.$http.get(api).then(response => {
        vm.isLoading = false
        if (response.data.success) {
          vm.cart = response.data.data
        } else {
          this.$bus.$emit('message:push', `取得購物車資訊失敗: ${response.data.message}`, 'danger')
        }
      })
    },
    addToCart (id, qty = 1) {
      const vm = this
      const api = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/cart`
      vm.status.loadingToCart = id
      const cart = {
        product_id: id,
        qty: qty
      }
      this.$http.post(api, { data: cart }).then(response => {
        vm.status.loadingToCart = ''
        if (response.data.success) {
          vm.getCart()
          $('#productModal').modal('hide')
        } else {
          this.$bus.$emit('message:push', `加入購物車失敗: ${response.data.message}`, 'danger')
        }
      })
    },
    removeCartItem (id) {
      const vm = this
      const api = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/cart/${id}`
      vm.isLoading = true
      this.$http.delete(api).then(response => {
        vm.isLoading = false
        if (response.data.success) {
          vm.getCart()
        } else {
          this.$bus.$emit('message:push', `移除購物車產品失敗: ${response.data.message}`, 'danger')
        }
      })
    },
    useCoupon () {
      const vm = this
      const api = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/coupon`
      const coupon = {
        code: vm.couponCode
      }
      vm.isLoading = true
      this.$http.post(api, { data: coupon }).then(response => {
        vm.isLoading = false
        if (response.data.success) {
          vm.getCart()
        } else {
          this.$bus.$emit('message:push', `套用優惠券失敗: ${response.data.message}`, 'danger')
        }
      })
    },
    createOrder () {
      const vm = this
      const api = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/order`
      const order = vm.form

      vm.isLoading = true
      this.$http.post(api, { data: order }).then(response => {
        vm.isLoading = false
        if (response.data.success) {
          vm.$router.push(`/customer_checkout/${response.data.orderId}`)
        } else {
          this.$bus.$emit('message:push', `建立訂單失敗: ${response.data.message}`, 'danger')
        }
      })
    }
  },
  created () {
    const sessionToken = document.cookie.replace(/(?:(?:^|.*;\s*)sessionToken\s*=\s*([^;]*).*$)|^.*$/, '$1')
    this.$http.defaults.headers.common.Authorization = sessionToken

    this.getProducts()
    this.getCart()
  }
}
</script>



